.tablescontainer {
  margin-top: 2%;
  margin-left: 4%;
  margin-right: 4%;
  padding-bottom: 2%;
  align-items: center;
  border-style: solid;
  border-color: #cacaca;
  border-width: 0.5px;
  border-radius: 20px;
  background-color: #fff;
  height: auto;
}

.tables {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: #424242;
}

.tablecontainergrid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin: 5% 5% 2% 5%;
  height:680px;
  overflow-y: scroll;
}

.tablecontainergrid::-webkit-scrollbar{
    display: none;
}

.innergrid{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    margin: 2% 0 -10% 0;
}


.emptytable {
