  margin-right: 5%;
}

.tablewaitermodalbox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 580px;
  height: 480px;
  display: flex;
  flex-direction: column;
  border: none;
  border-radius: 20px;
  background-color: #fffaf4;
}

.duplicatetablemodalbox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 445px;
  height: 259px;
  display: flex;
  flex-direction: column;
  border: none;
  border-radius: 20px;
  background-color: #fffaf4;
}

.duplicateinnerbox {
  padding: 10% 5% 0 5%;
}
.modalclose {
  display: flex;
  justify-content: flex-end;
  margin-top: 2%;
  margin-right: 1%;
}
.modalclosebutton {
  background-color: transparent;
  border: none;
}

